import React, { Component } from 'react';
import './NavBar.css'
import logo from '../assets/logo.png';
import resume from '../assets/ShivaniAggarwal_Resume.pdf';
import { NavLink, Link } from 'react-router-dom';

class NavBar extends Component {
constructor(props) {
    super(props);
    this.projectsRef = this.props.projectsRef;
	this.homeRef = this.props.homeRef;
    this.state = {
      active: 'home',
    };  
}

  componentDidMount() {
  window.addEventListener('scroll', this.handleScroll);
}

componentWillUnmount() {
  window.removeEventListener('scroll', this.handleScroll);
}

handleScroll = () => {
  if (window.location.pathname !== '/') return;

  const projects = document.getElementById('projects');
  const middleViewport = window.scrollY + window.innerHeight / 2;
  let active = 'home'; // default

  if (projects && middleViewport >= projects.offsetTop) {
    active = 'projects';
  }

  if (this.state.active !== active) {
    this.setState({ active });
  }
};


//     componentDidUpdate(prevProps) {
//     if (prevProps.location !== this.props.location) {
//       const hash = this.props.location.hash;
//       if (hash) {
//         const element = document.querySelector(hash);
//         if (element) {
//           element.scrollIntoView({ behavior: 'auto' }); // jumps to section
//         }
//       } else {
//         window.scrollTo(0, 0); // scroll to top if no hash
//       }
//     }
//   }

	render() {
		return (
		  <div>
		      <nav className="navbar fixed-top navbar-expand-sm custom-navbar justify-content-end">
		        <Link className="navbar-brand" to="/">
		        	<img src={logo} alt='logo' height='55px' width='55px'/>
		        </Link>
		        <button className="ml-auto navbar-toggler navbar-light" type="button" data-toggle="collapse" data-target="#navbarContent">
		            <span className="navbar-toggler-icon"></span>
		        </button>
		        <div className="ml-auto collapse navbar-collapse flex-grow-0" id="navbarContent">
		            <ul className="navbar-nav text-right">
		              <li className="nav-item">
						<NavLink className={() => window.location.pathname === '/' && this.state.active === 'home' ? 'nav-link active' : 'nav-link'} to="/" onClick={() => this.setState({active: 'home'})}>Home</NavLink>
		              </li>
					  <li className="nav-item">
						<NavLink className={() => window.location.pathname === '/' && this.state.active === 'projects' ? 'nav-link active' : 'nav-link'} to="/#projects" state={{ scrollTo: 'projects' }} onClick={() => this.setState({active: 'projects'})}>Projects</NavLink>
		              </li>
		              <li className="nav-item">
						<NavLink className={({ isActive }) => `nav-link ${isActive ? 'active' : ''}`} to="/about">About</NavLink>
		              </li>
					 {/* <li className="nav-item">
						<NavLink className={({ isActive }) => `nav-link ${isActive ? 'active' : ''}`} to="/contact">Contact</NavLink>
		              </li> */}
					 <li className="nav-item">
		                <a className={`nav-link`} href={resume} target='_blank' rel="noopener noreferrer">Resume</a>
		              </li>
		            </ul>
		        </div>
		      </nav>
		    </div>
		);
	}
}
export default NavBar;